export default {
	data() {
		return {
			showClose:false,
			searchContent:''
		}
	},
	methods:{
		input(){// 事件处理函数内实时修改当前值不生效
			setTimeout(() => {
				if(this.searchContent && this.searchContent.trim().length){
					this.showClose=true;
				}
				else{
					this.showClose=false;
				}
				
			}, 0)
			
		},
		clearsearch(){
			this.searchContent=""
		},
		search(){
			console.log("-search-")
		}
	}
}
